.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
/*******************************************************************************/


.header-wrapper{
  position:relative;
  background: url(./images/tshirt.jpg) no-repeat;
  background-size: cover;
  background-position:center;
  height:100vh;
  width: 100%;
  filter: brightness(100%);
  /* z-index: 2; */
}

.main-info{
  position: absolute;
  top:0;
  left:0;
  right:0;
  bottom:0;
  display: flex;
  flex-direction:column;
  justify-content: center;
  align-items: center;
  text-align: center;
 
}

.main-info h2{

  font-size: 50px;
  color:#000;
  text-shadow:
   -1px -1px 0 #fff,  
    1px -1px 0 #fff,
    -1px 1px 0 #fff,
     1px 1px 0 #fff;
  
}


.nav-link,.nav-links{
  
 
  color:white !important;
  font-weight: bold !important;
  text-shadow:
   -1px -1px 0 #000,  
   1px -1px 0 #000,
    -1px 1px 0 #000,
    1px 1px 0 #000;
}

.navbar-toggler{
  border: 2px solid #000 !important;
  color: #000;
  z-index: 1;
}

.typed-text{
  font-size: 35px !important;
  font-weight: bold;
  color:var(--primary);
  color:#fff;
  letter-spacing: 5px;
  text-shadow:
   -1px -1px 0 #000,  
   1px -1px 0 #000,
    -1px 1px 0 #000,
    1px 1px 0 #000;
}


/****************HEADER BUTTON******************/
.buttonBox{
  z-index: 2;
  border: 2px solid #000 ;
  padding: 10px 15px;
  font-size: 30px;
  text-decoration: none;
  color: #000;
  text-shadow:
   -1px -1px 0 #fff,  
   1px -1px 0 #fff,
    -1px 1px 0 #fff,
    1px 1px 0 #fff !important;

  padding:3px 5px;
  border-radius:5px;
}


.contact>a{
  transition:all .5s ease-in-out;
}

.contact:hover a{
  background:#121212;
  color:white;
  text-shadow:
   -1px -1px 0 #000,  
   1px -1px 0 #000,
    -1px 1px 0 #000,
    1px 1px 0 #000 !important;
}

.view>a{
  transition:all .3s ease-in-out;
}

.view:hover a{
  background:#000;
  color:skyblue;
  text-shadow:
   -1px -1px 0 #000,  
   1px -1px 0 #000,
    -1px 1px 0 #000,
    1px 1px 0 #000 !important;
}

.navbar-toggler-icon{
  color:#fff;
}


/***************CARDS******************/
.category{
  margin-top:20px !important;
  /* margin:20px; */
  padding: 5px;
  box-shadow: 3px 3px gray;
}



/***************BODY******************/

.sidenav{
 
  margin-right: 20px !important;
  
}


/************************ SMALL ***********************/
@media(max-width:767px ){
  .main-info{
    top:120px;
  }
  #shopname{
    font-size:25px;
  }
  .nav-links{
    font-size: 18px;
    text-decoration: none;
  }
  .nav-link{
    font-size: 18px;
    
  }
  .category{
    margin-left: 20px;
  }
  .cardbody{
    margin: 30px;
  }
  .contact{
    font-size: 20px;
  }

   #caro{
    margin-top: 20px !important;
   }
}


 






/************************ MEDIUM ***********************/

@media(max-width:1367px ) and (min-width:768){
  .nav-link{
    font-size: 30px;
    /* margin-left: 100px; */
  }

  .cardbody{
    margin: 50px;
  }
  

  .sidenav{
    margin-left: 10px;
    border-right: 1px solid #000;
  }
  .category{
    margin-left: 50px !important;
  }
}


/************************ Large ***********************/
@media(min-width:1368px){
  .header-wrapper{
    position:relative;
    background: url(./images/tshirt.jpg) no-repeat;
    background-size: cover;
    background-position:center;
    height:100vh;
    filter: brightness(110%);
  }

  .cardbody{
    margin-top: 70px;
  }
  
  .nav-link{
    font-size: 35px;
    margin-left: 180px;
  }

  .sidenav{
    margin-left: 10px;
    margin-top: 70px !important;
    
    border-right: 1px solid #000;
 
   
  }
  .nav-links{
  
    text-decoration: none;
  }
  
  #caro{
    margin-top:80px !important;
    margin-left: 40px;
    
  }

}

